<template>
  <div class="bottom">
    <div class="maininp">
      <input
        v-model="roomcode"
        ref="room"
        maxlength="6"
        @input="check(6)"
        placeholder="Enter Room Code"
        type="text"
      />
    </div>
    <input
      v-model="name"
      maxlength="20"
      ref="name"
      @input="ncheck(20)"
      placeholder="Enter your Name"
      type="text"
    />
    <router-link to="/chat"
      ><button class="primary">Connect</button></router-link
    >
    <button @click="$emit('toggle')" class="secondary">CREATE ROOM</button>
  </div>
</template>

<script>
export default {
  name: "Mobilejoin",
  methods: {
    check(num) {
      if (this.$refs.room.value.length > num) {
        this.$refs.room.value = this.$refs.room.value.substr(0, num);
      }
    },
    ncheck(num) {
      if (this.$refs.name.value.length > num) {
        this.$refs.name.value = this.$refs.name.value.substr(0, num);
      }
    },
  },
  computed: {
    name: {
      get() {
        return "";
      },
      set(value) {
        this.$store.dispatch("name", value);
      },
    },
    roomcode: {
      get() {
        return "";
      },
      set(value) {
        this.$store.dispatch("roomcode", value);
      },
    },
  },
};
</script>
<style lang="scss" scoped>
.bottom {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  input {
    background: #f0f0f0;
    box-shadow: 5px 10px 15px 2px rgba(0, 0, 0, 0.25);
    border-radius: 10px;
    margin: 2rem 0;
    height: 50px;
    border: none;
    outline: none;
    text-align: center;
    width: clamp(300px, 70vw, 400px);
    font-family: "Montserrat", sans-serif;
    font-weight: 500;
    font-size: 2.2rem;
    padding: 0 50px;
    &:focus {
      border: 1px solid rgb(127, 127, 233);
    }
  }
  .maininp {
    input {
      letter-spacing: 5px;
      font-weight: bold;
      &::placeholder {
        letter-spacing: normal;
        font-weight: normal;
      }
    }
    button {
      display: none;
    }
  }
  button {
    padding: 1.2rem 2rem;
    font-size: 2rem;
    color: #ffffff;
    border: none;
    outline: none;
    border-radius: 10px;
    box-shadow: 5px 10px 15px 2px rgba(0, 0, 0, 0.25);
    margin: 20px 0px;
    cursor: pointer;
  }
  .primary {
    background: #508a81;
  }
  .secondary {
    background: #ff8f80;
  }
}
</style>