<template>
  <div class="bottom">
    <input
      v-model="name"
      maxlength="20"
      placeholder="Enter your Name"
      type="text"
    />
    <div class="toggle">
      <h3>Personal</h3>
      <label class="switch">
        <input @click="toggle" type="checkbox" />
        <span class="slider round"></span>
      </label>
      <h3>Group</h3>
    </div>
    <input
      v-model="group"
      :class="[
        show ? 'input' : 'none',
        'animate__animated animate__fadeInDown',
      ]"
      placeholder="Name your group"
      type="text"
    />
    <button @click="create">CREATE ROOM</button>
    <button @click="$emit('toggle')" class="secondary">Join</button>
  </div>
</template>

<script>
export default {
  name: "Mobilecreate",
  methods: {
    toggle(e) {
      this.$store.dispatch("toggle");
    },
    async create() {
      const name = this.$store.state.name;
      const group = this.$store.state.group;
      const toggle = this.$store.state.toggle;
      const uri =
        process.env.NODE_ENV == "development"
          ? "http://localhost:8081"
          : location.origin;
      if (toggle) {
        if (name !== "" && group !== "") {
          const req = await fetch(
            `${uri}/api/create/group?name=${name}&&group=${group}`,
            {
              method: "POST",
            }
          );
          const res = await req.json();
          if (res.status === 200) {
            this.$store.dispatch("roomcode", res.id);
            this.$router.push({ path: "/chat" });
          }
        }
      } else {
        if (name !== "") {
          const req = await fetch(`${uri}/api/create?name=${name}`, {
            method: "POST",
          });
          const res = await req.json();
          if (res.status === 200) {
            this.$store.dispatch("roomcode", res.id);
            this.$router.push({ path: "/chat" });
          }
        }
      }
    },
  },
  computed: {
    show() {
      return this.$store.state.toggle;
    },
    name: {
      get() {
        return "";
      },
      set(value) {
        this.$store.dispatch("name", value);
      },
    },
    group: {
      get() {
        return "";
      },
      set(value) {
        this.$store.dispatch("group", value);
      },
    },
  },
};
</script>

<style lang="scss" scoped>
.bottom {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  input {
    background: #f0f0f0;
    box-shadow: 5px 10px 15px 2px rgba(0, 0, 0, 0.25);
    border-radius: 10px;
    margin: 2rem 0;
    height: 50px;
    border: none;
    outline: none;
    text-align: center;
    width: clamp(300px, 70vw, 400px);
    font-family: "Montserrat", sans-serif;
    font-weight: 500;
    font-size: 2.2rem;
    padding: 0 50px;
    &:focus {
      border: 1px solid rgb(127, 127, 233);
    }
  }
  button {
    padding: 1.2rem 2rem;
    font-size: 2rem;
    color: #ffffff;
    border: none;
    outline: none;
    border-radius: 10px;
    box-shadow: 5px 10px 15px 2px rgba(0, 0, 0, 0.25);
    margin: 20px 0px;
    cursor: pointer;
    background: #ff8f80;
  }
  .toggle {
    width: 80%;
    display: flex;
    justify-content: center;
    align-items: center;
    h3 {
      margin: 0px 20px;
      color: black;
      font-size: 2rem;
    }
  }
  .switch {
    position: relative;
    display: inline-block;
    width: 60px;
    height: 34px;
  }
  /* Hide default HTML checkbox */
  .switch input {
    opacity: 0;
    width: 0;
    height: 0;
  }
  /* The slider */
  .slider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #ccc;
    -webkit-transition: 0.4s;
    transition: 0.4s;
  }
  .slider:before {
    position: absolute;
    content: "";
    height: 26px;
    width: 26px;
    left: 4px;
    bottom: 4px;
    background-color: white;
    -webkit-transition: 0.4s;
    transition: 0.4s;
  }
  input:checked + .slider {
    background-color: #2196f3;
  }
  input:focus + .slider {
    box-shadow: 0 0 1px #2196f3;
  }
  input:checked + .slider:before {
    -webkit-transform: translateX(26px);
    -ms-transform: translateX(26px);
    transform: translateX(26px);
  }
  /* Rounded sliders */
  .slider.round {
    border-radius: 34px;
  }
  .slider.round:before {
    border-radius: 50%;
  }
  .secondary {
    background: #508a81;
  }
}
</style>