<template>
  <div>
    <Mobile v-if="mobile" />
    <Desktop v-else />
    <Popup @show="show" :class="[pop ? '' : 'hide']" msg="No room found!" />
  </div>
</template>

<script>
import Mobile from "./Mobile";
import Desktop from "./Desktop";
import Popup from "./Popup";
export default {
  name: "Homepage",
  components: {
    Mobile,//: () => import('./Mobile'),
    Desktop,//: () => import('./Desktop'),
    Popup//: () => import('./Popup'),
  },
  data() {
    return {
      mobile: true,
      pop: false
    };
  },
  mounted() {
    this.rscreen();
    window.addEventListener("resize", (e) => {
      this.rscreen();
    });
  },
  methods: {
    rscreen() {
      if (window.innerWidth < 1420) {
        this.mobile = true;
      } else {
        this.mobile = false;
      }
    },
    show() {
      this.pop = true
      setTimeout(() => {
        this.pop = false
      }, 4000);
    }
  },
};
</script>
<style scoped>
.hide {
  display: none;
}
</style>