import { createStore } from 'vuex'

export default createStore({
  state: {
    title: 'Hakerspace',
    roomcode: '',
    name: '',
    group: '',
    noroom: 0,
    toggle: false
  },
  mutations: {
    name(state, value) {
      state.name = value
    },
    group(state, value) {
      state.group = value
    },
    roomcode(state, value) {
      state.roomcode = value
    },
    setTitle(state, value) {
      if (state.title !== 'Hakerspace') {
        state.title = value
      } else {
        return
      }
    },
    noroom(state) {
      state.noroom += 1
    },
    toggle(state) {
      state.toggle = !state.toggle
    }
  },
  actions: {
    name({commit}, value) {
      commit('name', value);
    },
    group({commit}, value) {
      commit('group', value);
    },
    roomcode({commit}, value) {
      commit('roomcode', value);
    },
    setTitle({commit}, value) {
      commit('setTitle', value);
    },
    noroom({commit}) {
      commit('noroom');
    },
    toggle({commit}) {
      commit('toggle')
    }
  },
  getters:{
    noroom(state) {
      return state.noroom
    }
  },
  modules: {
  }
})
