<template>
  <div class="card">
    <h1>{{ msg }}</h1>
  </div>
</template>

<script>
export default {
  name: "Popup",
  props: {
    msg: String,
  },
  emits: ['show'],
  computed: {
    popup() {
      return this.$store.getters.noroom;
    },
  },
  watch: {
    popup() {
      this.$emit("show");
    },
  },
};
</script>

<style lang="scss" scoped>
.card {
  position: absolute;
  top: 5%;
  left: 50%;
  transform: translateX(-50%);
  width: clamp(300px, 40vw, 350px);
  height: 100px;
  color: black;
  background: rgba(233, 233, 233, 0.815);
  border: 1px solid rgba(0, 0, 0, 0.349);
  padding: 1em;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 15px;
  animation: slide-in 5s ease forwards;
  overflow: hidden;
}
@keyframes slide-in {
  0% {
    transform: translate(-50%, -200px);
  }
  50% {
    transform: translate(-50%, 0);
  }
  100% {
    transform: translate(-50%, -200px);
  }
}
</style>